
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  width: 100%;
  height: 100%;
  overflow: scroll;
}
* {
  margin: 0;
  padding: 0;
}
html, body {
  width: 100%;
  min-width:1200px;
  height: 100%;
}
::-moz-selection{
  color:white;
  background: #000;
}
::selection{
  color:white;
  background: #000;
}



