}

/* body */
#column-compare[data-v-3f735490] {
  position: fixed;
  width: 100%;
  height: 0;
  top: 0;
  left: 0;
  display: none;
  background: white;
  z-index: 1000;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-top: 60px;
}

