
























































































/* Close button */
.btn-close[data-v-7938e1fe] {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  right: -70px;
  top: -70px;
  color: white;
  border: none;
  background: gray;
  font-size: 70px;
  opacity: 0.5;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  /*right: 2.5%;*/
  /*top: 10px;*/
}
.btn-close[data-v-7938e1fe]:hover {
  right: -50px;
  top: -50px;
  opacity: 1;
  padding: 20px 30px 20px 0;
}
.btn-close[data-v-7938e1fe]:focus,
.btn-close[data-v-7938e1fe]:active:focus,
.btn-close.active[data-v-7938e1fe]:focus,
.btn-close.focus[data-v-7938e1fe],
.btn-close:active.focus[data-v-7938e1fe],
.btn-close.active.focus[data-v-7938e1fe] {
  outline: none;
}

/* Main page */
#code-compare[data-v-7938e1fe] {
  width: 100%;
  height: 0;
  position: fixed;
  top: 0;
  left: 0;
  background: #1B2426;
  z-index: 1000;
  display: none;
}
.half[data-v-7938e1fe] {
  width: 50%;
  height: 100%;
  float: left;
}
.left[data-v-7938e1fe] {
  border-right: 1px dotted blue;
}

/* Title,about database name and the function or procedure name */
.title[data-v-7938e1fe] {
  text-align: center;
  height: 30px;
  margin: 20px 0 10px 5%;
  color: #5BA1CF;
  opacity:0;
  font-size:28px;
  line-height: 30px;
  overflow: hidden;
  max-width:90%;
  white-space:nowrap;
}
.left .title[data-v-7938e1fe]{
  margin-left:-30px;
}
.right .title[data-v-7938e1fe]{
  margin-left:30px;
}

/* code block */
.code-block[data-v-7938e1fe] {
  width: 90%;
  height: calc(95% - 60px);
  margin: 2.5% 5%;
  background: #1B2426;
  overflow-Y: scroll;
  margin-top:30px;
  opacity:0;
}



